import { Component, Input, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService } from '../../services/login.service';
import { GlobalObject } from '../../Globals/GlobalObject';
import { ContentBaseClass } from '../base/content.base.class';

@Component({
    selector: 'choreography',
    templateUrl: '../base/content.base.template.html',
    styleUrls: ['./choreography.component.scss']
})
export class ChoreographyComponent extends ContentBaseClass implements OnInit {

    // 'Meta' data
    contentName: string = GlobalObject.contentName['choreography'];
    contentsName: string = GlobalObject.contentsName['choreography'];
    texts = GlobalObject.texts['choreography'];


    @Input() customStyles: string;

    constructor(protected route: ActivatedRoute, protected router: Router,
            protected http: HttpClient, protected loginService: LoginService) {

        super(route, router, http, loginService);
    }
}



