import { Component, OnInit }  from '@angular/core';
import { HttpClient }         from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { IMultiSelectOption } from 'angular-2-dropdown-multiselect';
import { LoginService }       from '../../services/login.service';
import { GlobalObject }       from '../../Globals/GlobalObject';
import { AddObjectComponent } from '../base/add-object.base.class';

@Component({
    selector: 'app-add-choreography',
    templateUrl: '../base/add-object.base.template.html',
    styleUrls: ['./add-choreography.component.scss']
})
export class AddChoreographyComponent extends AddObjectComponent implements OnInit {

    // 'Meta' data
    contentName: string = GlobalObject.contentName['choreography'];
    contentsName: string = GlobalObject.contentsName['choreography'];
    texts = GlobalObject.texts['choreography'];

    // Component body
    styles: IMultiSelectOption[];
    // Multiselect box settings/texts
    multiselectBoxTexts = GlobalObject.mutliselectBoxTexts;
    multiselectBoxSettings = {
        buttonClasses: 'btn btn-info',
        itemClasses: ''
    };
    multiselectBoxTextsSolo = {
        defaultTitle: 'Выберите стиль'
    }
    multiselectBoxSettingsSolo = {
        buttonClasses: 'btn btn-info settings-style-change-box-button',
        itemClasses: '',
        selectionLimit: 1,
        autoUnselect: true,
        closeOnSelect: true
    }
    selectedStylesPreviewModel = [];

    // Styles for delete
    stylesForDeleteModel = [];
    // Style for change
    styleForChangeModel = [];

    constructor(protected route: ActivatedRoute, protected router: Router,
            protected http: HttpClient, protected loginService: LoginService) {

        super(route, router, http, loginService);

        this.callbacks.push(() => {
            // Получить стили
            this.getStyles(() => {
                // "Вытянуть" из стилей преподавателя только массив id
                var selectedStyles = [];
                for (var i = 0; i < this.obj.styles.length; i++)
                    selectedStyles.push(this.obj.styles[i].id);
                this.obj.styleIds = selectedStyles;
                this.styleSelected();

                // Удалить поле styles из obj (теперь у нас есть styleIds)
                delete this.obj.styles;
            });
        });
    }


    // Get all possible styles from server
    getStyles(callback?: Function) {
        this.http.get(GlobalObject.serverAddress + '/api/styles').subscribe((data) => {
            console.log(data['message']);
            this.styles = data['data'];

            callback && callback();
        });
    }


    styleSelected() {
        // Преобразовать массив выбранных стилей в массив, отправляемый в предпросмотр (если включено)
        this.selectedStylesPreviewModel = [];
        for (var i = 0; i < this.obj.styleIds.length; i++)
            for (var j = 0; j < this.styles.length; j++)
                if (this.styles[j].id == this.obj.styleIds[i]) {
                    this.selectedStylesPreviewModel.push(this.styles[j]);
                    break;
                }
    }


    // Styles settings function
    addStyle(styleName) {
        if (!styleName) {
            alert('Введите имя стиля');
            return;
        }

        this.http.post('/api/admin/styles', {name: styleName}).subscribe((data) => {
            if (data['state'] === 'success')
                alert('Добавлено');
            else 
                alert('Ошибка');

            // Получить обновлённые стили
            this.getStyles();
        });
    }

    deleteStyles() {
        if (this.stylesForDeleteModel.length == 0) {
            alert('Выберите хотя бы один стиль');
            return;
        }

        this.http.delete('/api/admin/styles', this.stylesForDeleteModel).subscribe((data) => {
            if (data['state'] === 'success')
                alert('Удалено');
            else 
                alert('Ошибка');

            // Получить обновлённые стили
            this.getStyles();
        });
    }

    changeStyle(styleName) {
        if (this.styleForChangeModel.length != 1) {
            alert('Выберите стиль');
            return;
        }
        if (!styleName) {
            alert('Введите новое имя стиля');
            return;
        }

        this.http.put('/api/admin/styles', {id: this.styleForChangeModel[0], name: styleName}).subscribe((data) => {
            if (data['state'] === 'success')
                alert('Изменено');
            else 
                alert('Ошибка');

            // Получить обновлённые стили
            this.getStyles();
        });
    }
}



