import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LoginService } from '../../services/login.service';
import { GlobalObject } from '../../Globals/GlobalObject';
import { ContentsBaseClass } from '../base/contents.base.class';

@Component({
    selector: 'affiches',
    templateUrl: '../base/contents.base.template.html',
    styleUrls: ['./affiches.component.scss']
})
export class AffichesComponent extends ContentsBaseClass implements OnInit {
    
    // 'Meta' data
    contentName: string = GlobalObject.contentName['affiche'];
    contentsName: string = GlobalObject.contentsName['affiche'];
    texts = GlobalObject.texts['affiche'];

    constructor(protected http: HttpClient, protected loginService: LoginService) {

        super(http, loginService);
    }
}



