import { Component, Input, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService } from '../../services/login.service';
import { GlobalObject } from '../../Globals/GlobalObject';
import { ContentBaseClass } from '../base/content.base.class';

@Component({
    selector: 'affiche',
    templateUrl: '../base/content.base.template.html',
    styleUrls: ['./affiche.component.scss']
})
export class AfficheComponent extends ContentBaseClass implements OnInit {

    // 'Meta' data
    contentName: string = GlobalObject.contentName['affiche'];
    contentsName: string = GlobalObject.contentsName['affiche'];
    texts = GlobalObject.texts['affiche'];


    @Input() customDate: string;
    
    constructor(protected route: ActivatedRoute, protected router: Router,
            protected http: HttpClient, protected loginService: LoginService) {

        super(route, router, http, loginService);

        // Будут выполнены после загрузки компонента (в конце ngOnInit)
        this.callbacks.push(() => {
            // Преобразование даты к понятному пользователю виду
            var date = new Date(this.obj.date),
                month = '' + (date.getMonth() + 1),
                day = '' + date.getDate(),
                year = date.getFullYear();

            if (month.length < 2) month = '0' + month;
            if (day.length < 2) day = '0' + day;

            this.obj.date = [day, month, year].join('.');
        });
    }
}



