import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LoginService } from '../../services/login.service';
import { GlobalObject } from '../../Globals/GlobalObject';
import { ContentsBaseClass } from '../base/contents.base.class';

@Component({
    selector: 'projects',
    templateUrl: '../base/contents.base.template.html',
    styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent extends ContentsBaseClass implements OnInit {

    // 'Meta' data
    contentName: string = GlobalObject.contentName['project'];
    contentsName: string = GlobalObject.contentsName['project'];
    texts = GlobalObject.texts['project'];

    constructor(protected http: HttpClient, protected loginService: LoginService) {

        super(http, loginService);
    }
}



