import { Component, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService }     from '../../services/login.service';
import { GlobalObject }     from '../../Globals/GlobalObject';

interface uploadedImg {
    id: number,
    isUploadable: boolean,
    text: string,
    isDeletable: boolean
};

export abstract class AddObjectComponent {

    // 'Meta' data
    contentName: string = 'NOT_DEFINED';
    contentsName: string = 'NOT_DEFINED';
    texts = {};


    // Component body
    afficheId: string;
    changeAffiche: boolean;

    preview: boolean = false;
    obj: any = {
        title: '',
        img: '',
        description: '',
        text: '',
    };

    // Uploaded imgs model (sending them to server)
    uploadedImgs: [uploadedImg] = [
        {id: 0, isUploadable: true, text: '...', isDeletable: false}
    ];

    callbacks: Array<Function> = [];

    constructor(protected route: ActivatedRoute, protected router: Router,
            protected http: HttpClient, protected loginService: LoginService) {

        this.route.params.subscribe(params => {
            this.afficheId = params['id'];
        });
    }

    ngOnInit() {
        if (this.afficheId) {
            this.http.get(GlobalObject.serverAddress + '/api/' + this.contentName + '/' + this.afficheId).subscribe(data => {
                console.log(data['message']);
                /*this.obj.title = data['data']['title'];
                this.obj.img = data['data']['img'];
                this.obj.description = data['data']['description'];
                this.obj.text = data['data']['text'];*/
                this.obj = data['data'];
                this.changeAffiche = true;

                // Callbacks
                for(var i = 0; i < this.callbacks.length; i++) {
                    this.callbacks[i]();
                }
            });
        } else {
            this.changeAffiche = false;

            // Callbacks
            for(var i = 0; i < this.callbacks.length; i++) {
                this.callbacks[i]();
            }
        }
    }

    addObject() {
        if (!this.obj.title) {
            console.log('Введите заголовок');
        } else if (!this.obj.text) {
            console.log('Введите текст афиши');
        } else {
            // Adding or changing existing affiche
            if (this.changeAffiche) {
                // Changing
                // Change it? (pass contentName as parameter? and all parameters is in the object, that can be extended in child component)
                this.loginService.changeObject(this.contentName, this.afficheId, this.obj, (state: string) => {
                            
                    if (state === 'success') {
                        alert('Изменено');
                        this.router.navigate(['/' + this.contentName, this.afficheId]);
                    }
                });
            } else {
                // Adding
                this.loginService.addObject(this.contentName, this.obj, (state: string) => {

                    if (state === 'success') {
                        alert('Добавлено');
                        this.router.navigate(['/' + this.contentsName]);
                    }
                });
                
            }
        }
    }

    previewObject() {
        this.preview = !this.preview;
    }


    // Imgs upload interface
    // TODO: Отправить в параметрах файл, выгруженный в input type="file"
    uploadImg(uploadedImg: uploadedImg, file: File) {
        this.uploadedImgs.push({
            id: this.uploadedImgs.length,
            isUploadable: true,
            text: '...',
            isDeletable: false
        });

        uploadedImg.isUploadable = false;

        // TODO: Запрос с картинкой в объекте через multipart/form-data
        var formData = new FormData();
        formData.append('file', file);
        this.http.post(GlobalObject.serverAddress + '/api/admin/img', formData).subscribe((data) => {
            console.log(data['message']);

            if (data['state'] === 'success')
                uploadedImg.text = GlobalObject.serverAddress + '/' + data['link'].replace('\\', '/');
            else {
                uploadedImg.text = 'Error';
            }
            
            uploadedImg.isDeletable = true;
        });
    }

    copyImgLinkToClipboard(imgLinkInput: HTMLInputElement) {
        imgLinkInput.select();
        document.execCommand('copy');
    }

    deleteImg(uploadedImg: uploadedImg) {
        // Запрос на удаление картинки (по её адресу)
        this.http.delete(GlobalObject.serverAddress + '/api/admin/img/' + encodeURIComponent(uploadedImg.text)).subscribe((data) => {
            console.log(data['message']);

            for (var i = 0; i < this.uploadedImgs.length; i++)
                if (uploadedImg.id == this.uploadedImgs[i].id) {
                    //delete this.uploadedImgs[i];
                    this.uploadedImgs.splice(i, 1);
                }
        });
    }
}



