import { Injectable }   from '@angular/core';
import { HttpClient }   from '@angular/common/http';
import { GlobalObject } from '../Globals/GlobalObject';

@Injectable()
export class LoginService {

    public username: string = null;

    constructor(private http: HttpClient) {
        this.checkLogin();
    }


    // Стартовая проверка логина
    checkLogin() {
        this.http.get(GlobalObject.serverAddress + '/api/admin/login').subscribe(data => {
            // Если уже залогинен, то взять имя из ответа
            data['username'] && (this.username = data['username']);
        });
    }

    login(username: string, password: string) {
        // Here should be a server request
        this.http.post(GlobalObject.serverAddress + '/api/admin/login',
                {username: username, password: password}).subscribe(data => {

            if (data['state'] === 'success') {
                this.username = username;
                console.log('[LoginService] Logged in as ' + username);
            } else {
                alert('Неверный логин или пароль');
                console.log('[LoginService] Login failed');
            }
        });
    }

    logout() {
        this.http.delete(GlobalObject.serverAddress + '/api/admin/login').subscribe(data => {
            this.username = null;
            console.log('[LoginService] Logged out');
        });
    }

    changePassword(oldPass: string, newPass: string) {
        this.http.post(GlobalObject.serverAddress + '/api/admin/change-password',
                {oldPass: oldPass, newPass: newPass}).subscribe(data => {

            data['message'] && console.log(data['message']);
            if (data['state'] === 'success')
                alert('Пароль изменён');
        });
        console.log('[LoginService] Changing password...');
    }


    // FUNCTIONS TO WORK WITH CONTENT

    addObject(contentName: string, obj: any, callback?: Function) {

        this.http.post(GlobalObject.serverAddress + '/api/admin/' + contentName,
                obj).subscribe(data => {

            data['message'] && console.log(data['message']);
            callback && callback(data['state']);
        });
    }

    changeObject(contentName: string, id: string, obj: any, callback?: Function) {

        this.http.put(GlobalObject.serverAddress + '/api/admin/' + contentName + '/' + id,
                obj).subscribe(data => {

            data['message'] && console.log(data['message']);
            callback && callback(data['state']);
        });
    }

    deleteObject(contentName: string, id: string, callback?: Function) {
        this.http.delete(GlobalObject.serverAddress + '/api/admin/' + contentName + '/' + id).subscribe(data => {
            data['message'] && console.log(data['message']);
            callback && callback(data['state']);
        })
    }
}


