import { BrowserModule }           from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule }        from '@angular/common/http';
import { FormsModule }             from '@angular/forms';
import { NgModule }                from '@angular/core';

import { AppRoutingModule }  from './app-routing.module';
import { LoginService }      from './services/login.service';
import { SafeHtmlPipe,
  FormatDateFromBootstrap }  from './pipes';
import { CKEditorModule }    from 'ng2-ckeditor';
import { NgbModule }         from '@ng-bootstrap/ng-bootstrap';
import { MultiselectDropdownModule } from 'angular-2-dropdown-multiselect';

import { AppComponent }      from './app.component';
import { ContentComponent }  from './content/content.component';
import { AfficheComponent }  from './content/affiche/affiche.component';
import { ProjectComponent }     from './content/project/project.component';

import { AffichesComponent }   from './content/affiches/affiches.component';
import { ProjectsComponent }    from './content/projects/projects.component';
import { TeploobmenComponent } from './content/teploobmen/teploobmen.component';

import { AdminComponent }      from './admin/admin.component';
import { AddAfficheComponent } from './admin/add-affiche/add-affiche.component';
import { AddProjectComponent } from './admin/add-project/add-project.component';
import { ChoreographyComponent } from './content/choreography/choreography.component';
import { ChoreographiesComponent } from './content/choreographies/choreographies.component';
import { AddChoreographyComponent } from './admin/add-choreography/add-choreography.component';

@NgModule({
  declarations: [
    SafeHtmlPipe,
    FormatDateFromBootstrap,
    AppComponent,
    ContentComponent,
    AfficheComponent,
    ProjectComponent,
    AffichesComponent,
    ProjectsComponent,
    TeploobmenComponent,
    AdminComponent,
    AddAfficheComponent,
    AddProjectComponent,
    ChoreographyComponent,
    ChoreographiesComponent,
    AddChoreographyComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    AppRoutingModule,
    CKEditorModule,
    MultiselectDropdownModule,
    NgbModule.forRoot()
  ],
  providers: [
    LoginService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



