import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LoginService } from '../../services/login.service';
import { GlobalObject } from '../../Globals/GlobalObject';

export abstract class ContentsBaseClass {

    // 'Meta' data
    contentName: string = 'NOT_DEFINED';
    contentsName: string = 'NOT_DEFINED';
    texts = {};
    
    
    // Component body
    objects: Array<{}>;
    callbacks: Array<Function> = [];

    constructor(protected http: HttpClient, protected loginService: LoginService) { }

    ngOnInit() {
        this.http.get(GlobalObject.serverAddress + '/api/' + this.contentsName).subscribe(data => {
            console.log(data['message']);
            this.objects = data['data'];

            // Callbacks
            for(var i = 0; i < this.callbacks.length; i++) {
                this.callbacks[i]();
            }
        });
    }
}



