import { Component, OnInit } from '@angular/core';
import { HttpClient }        from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService }      from '../../services/login.service';
import { GlobalObject }      from '../../Globals/GlobalObject';
import { AddObjectComponent } from '../base/add-object.base.class';

@Component({
    selector: 'add-affiche',
    templateUrl: '../base/add-object.base.template.html',
    styleUrls: ['./add-affiche.component.scss']
})
export class AddAfficheComponent extends AddObjectComponent implements OnInit {

    // 'Meta' data
    contentName: string = GlobalObject.contentName['affiche'];
    contentsName: string = GlobalObject.contentsName['affiche'];
    texts = GlobalObject.texts['affiche'];

    
    constructor(protected route: ActivatedRoute, protected router: Router,
            protected http: HttpClient, protected loginService: LoginService) {

        super(route, router, http, loginService);

        this.callbacks.push(() => {
            var date = this.obj.date ? new Date(this.obj.date) : new Date();
            this.obj.date = {
                day: date.getDate(),
                month: date.getMonth() + 1,
                year: date.getFullYear()
            }
        });
    }

    addObject() {
        // Преобразование даты в нужный серверу вид
        var date = this.obj.date,
            month = '' + date.month,
            day = '' + date.day,
            year = date.year;

        if (month.length < 2) month = '0' + month;
        if (day.length < 2) day = '0' + day;

        // yyyy-mm-dd
        this.obj.date = [year, month, day].join('-');

        // Вызов родительской функции
        this.constructor.prototype.__proto__.addObject.apply(this);
    }
}



