import { Component, OnInit } from '@angular/core';
import { HttpClient }        from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService }      from '../../services/login.service';
import { GlobalObject }      from '../../Globals/GlobalObject';
import { AddObjectComponent } from '../base/add-object.base.class';

@Component({
    selector: 'add-project',
    templateUrl: '../base/add-object.base.template.html',
    styleUrls: ['./add-project.component.scss']
})
export class AddProjectComponent extends AddObjectComponent implements OnInit {

    // 'Meta' data
    contentName: string = GlobalObject.contentName['project'];
    contentsName: string = GlobalObject.contentsName['project'];
    texts = GlobalObject.texts['project'];


    constructor(protected route: ActivatedRoute, protected router: Router,
            protected http: HttpClient, protected loginService: LoginService) {

        super(route, router, http, loginService);
    }
}



