import { Component, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService } from '../../services/login.service';
import { GlobalObject } from '../../Globals/GlobalObject';

export abstract class ContentBaseClass {

    // 'Meta' data
    contentName: string = 'NOT_DEFINED';
    contentsName: string = 'NOT_DEFINED';
    texts = {};


    // Component body
    @Input() customTitle: string;
    @Input() customText: string;
    customObject: boolean = false;

    id: string;
    obj: any = {title: '', text: ''};

    callbacks: Array<Function> = [];

    constructor(protected route: ActivatedRoute, protected router: Router,
            protected http: HttpClient, protected loginService: LoginService) {
        
        // Wait until :id param is fetched
        this.route.params.subscribe(params => {
            this.id = params['id'];
        });
    }

    ngOnInit() {
        if (this.customTitle || this.customText) {
            this.customObject = true;
        } else {
            this.http.get(GlobalObject.serverAddress + '/api/' + this.contentName + '/' + this.id).subscribe(data => {
                console.log(data['message']);
                this.obj = data['data'];

                // Callbacks
                for(var i = 0; i < this.callbacks.length; i++) {
                    this.callbacks[i]();
                }
            });
        }
    }


    // TODO: change it? ('deleteObject' with contentName parameter?)
    deleteObject() {
        this.loginService.deleteObject(this.contentName, this.id, (state: string) => {
            if (state === 'success')
                alert('Удалено');
            else
                alert('Ошибка');

            this.router.navigate(['/' + this.contentsName, this.id]);
        });
    }
}


