import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LoginService } from '../../services/login.service';
import { GlobalObject } from '../../Globals/GlobalObject';
import { ContentsBaseClass } from '../base/contents.base.class';

@Component({
    selector: 'choreographies',
    templateUrl: '../base/contents.base.template.html',
    styleUrls: ['./choreographies.component.scss']
})
export class ChoreographiesComponent extends ContentsBaseClass implements OnInit {

    // 'Meta' data
    contentName: string = GlobalObject.contentName['choreography'];
    contentsName: string = GlobalObject.contentsName['choreography'];
    texts = GlobalObject.texts['choreography'];


    // Component body
    styles: [{ id: number, name: string }];
    // Model for multiselect box
    selectedStyles: [number];
    // Multiselect box settings/texts
    multiselectBoxTexts = GlobalObject.mutliselectBoxTexts;
    multiselectBoxSettings = {
        buttonClasses: 'btn btn-info'
    };

    constructor(protected http: HttpClient, protected loginService: LoginService) {

        super(http, loginService);

        this.callbacks.push(() => {
            // Get all possible styles from server
            this.http.get(GlobalObject.serverAddress + '/api/styles').subscribe((data) => {
                console.log(data['message']);
                this.styles = data['data'];
            });
        });
    }


    stylesSelected() {
        // Получить список учителей с выбранными стилями
        this.http.get(GlobalObject.serverAddress + '/api/choreographies/' + this.selectedStyles.join(',')).subscribe((data) => {
            console.log(data['message']);
            this.objects = data['data'];
        });
    }
}



